.projects {
  position: relative;
  width: 100%;
  max-width: 1300px;
  margin: 0 auto;
  padding-bottom: 20vh;
}
.projects .title_session_projects {
  font-size: 27px;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  gap: 1vw;
  padding: 5vh 0 0 0;
  border-bottom: 6px solid #000;
  max-width: 30%;
  font-weight: 100;
}
.projects .title_session_projects .number_session_projects {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  background-color: #000;
  color: #fff;
  padding: 20px;
}
.projects .session_projects {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  border-top: 1px solid #ccc;
  gap: 50px;
}
.projects .session_projects .list_projects {
  padding: 2vw 0;
  width: 50%;
}
.projects .session_projects .list_projects .project {
  cursor: pointer;
  padding: 10px 2vw;
  border-bottom: 1px solid #ccc;
  transition: 0.5s;
  overflow: hidden;
  height: 55px;
  position: relative;
}
.projects .session_projects .list_projects .project .name_project {
  font-size: 22px;
  font-weight: 100;
}
.projects .session_projects .list_projects .project .wraper_project {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 30px;
}
.projects .session_projects .list_projects .project .wraper_project .arrow_project {
  width: 40px;
  height: 40px;
  z-index: 1;
}
.projects .session_projects .list_projects .project .wraper_project .arrow {
  font-size: 45px;
  position: absolute;
  right: 1.4vw;
  transition: 0.5s;
}
.projects .session_projects .list_projects .project .description_project {
  margin: 15px 0;
  max-width: 350px;
  overflow-y: auto;
  max-height: 100px;
}
.projects .session_projects .list_projects .project .view_image {
  margin-right: 20px;
  font-size: 20px;
  transition: 0.5s;
  margin-bottom: 5px;
}
.projects .session_projects .list_projects .project .view_image:hover {
  background-color: #000;
  color: #fff;
  border: 1px solid #000;
  border-radius: 5px;
  padding: 8px 15px;
}
.projects .session_projects .list_projects .project a {
  color: #000;
  font-size: 20px;
  text-decoration: none;
  transition: 0.5s;
  margin-bottom: 5px;
}
.projects .session_projects .list_projects .project a svg {
  font-size: 20px;
  margin-right: 10px;
}
.projects .session_projects .list_projects .project a:hover {
  background-color: #000;
  color: #fff;
  border: 1px solid #000;
  border-radius: 5px;
  padding: 10px 15px;
}
.projects .session_projects .list_projects .project .animation_border {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 1px;
  background-color: #000;
  transition: width 0.6s ease !important;
}
.projects .session_projects .list_projects .project .buttonAllProjects {
  width: 100%;
  height: 90px;
}
.projects .session_projects .slide_projects {
  position: relative;
  width: 50%;
  height: 400px;
}
.projects .session_projects .slide_projects img {
  opacity: 0;
  position: absolute;
  top: 0;
  right: 0;
  max-height: 100%;
  max-width: 100%;
}
.projects .session_projects .slide_projects .selected {
  opacity: 1;
  z-index: 1;
}

@media (max-width: 768px) {
  .session_projects {
    flex-wrap: wrap;
  }
  .list_projects {
    order: 2;
    width: 100% !important;
    display: block;
  }
  .slide_projects {
    height: 300px !important;
  }
}
@media (max-width: 500px) {
  .session_projects {
    width: 100%;
  }
  .projects {
    padding-bottom: 0;
  }
  .slide_projects {
    position: static;
    width: 100% !important;
    height: 400px !important;
  }
  .slide_projects img {
    height: 100%;
    width: 100%;
  }
}